
.detail {
  height: 100%;
}
.detail .detailMain {
    width: 7.5rem;
    margin-top: 1.28rem;
    overflow: hidden;
}
.detail .detailMain .detailtitle {
      width: 100%;
      padding: .52rem .3rem 0;
      text-align: center;
}
.detail .detailMain .detailtitle .detailflag {
        float: left;
        width: .08rem;
        height: .24rem;
        margin: .14rem 0;
        background-color: #d63333;
}
.detail .detailMain .detailtitle .detailname {
        float: left;
        width: 90%;
        text-indent: .12rem;
        line-height: .5rem;
        font-size: .36rem;
        text-align: left;
}
.detail .detailMain .detailcontainer {
      width: 7rem;
      padding: .22rem .36rem 0;
      overflow: hidden;
}
.detail .detailMain .detailcontainer ol {
        overflow: hidden;
}
.detail .detailMain .detailcontainer ol li {
        /*ie6-ie9*/
}
.detail .detailMain .detailcontainer ol li .detailItems {
          line-height: .7rem;
          font-size: .28rem;
          color: #666;
}
.detail .detailMain .detailcontainer ol li .fl {
          float: left;
}
.detail .detailMain .detailcontainer ol li .detailexplainItems {
          line-height: .7rem;
          font-size: .28rem;
          color: #151515;
}
.detail .detailMain .detailcontainer ol li .detailmemberItems {
          float: left;
          line-height: .7rem;
          font-size: .28rem;
          color: #4990e2;
}
.detail .detailMain .detailcontainer ol li .detailprogressCount {
          float: left;
          line-height: .7rem;
          font-size: .28rem;
          color: #f6a623;
          margin-left: .13rem;
}
.detail .detailMain .detailcontainer ol li .detailprogressItems {
          width: 50%;
          height: .1rem;
          margin: .28rem 0;
          background-color: #fff;
          display: inline-block;
          border: 1px solid #f6a623;
          color: #f6a623;
          /*IE10*/
}
.detail .detailMain .detailcontainer ol li progress ie {
          display: block;
          height: 100%;
          background: #f6a623;
}
.detail .detailMain .detailcontainer ol li progress::-moz-progress-bar {
          background: #f6a623;
}
.detail .detailMain .detailcontainer ol li progress::-webkit-progress-bar {
          background: #fff;
}
.detail .detailMain .detailcontainer ol li progress::-webkit-progress-value {
          background: #f6a623;
}
.detail .detailMain .detailcontainer .detailwrapper {
        width: 100%;
        overflow: auto;
}
.detail .detailMain .detailcontainer .detailwrapper .detaildrawing-min {
          width: 160%;
          height: 5.03rem;
}
.detail .detailMain .detailcontainer .detailwrapper .detaildrawing-mid {
          width: 170%;
          height: 5.03rem;
}
.detail .detailMain .detailcontainer .detailwrapper .detaildrawing-max {
          width: 180%;
          height: 5.03rem;
}
.detail .detailMain .detailcontainer .detailwrapper .detaildrawing-smax {
          width: 210%;
          height: 5.03rem;
}
[v-cloak] {
  display: none;
}
.vShow {
  visibility: visible;
}
.vHide {
  visibility: hidden;
}



